

module.exports = {


    /**
     * 
     * @param {int} _score 
     * @param {*} success  
     * @param {*} fail     
     * @param {*} complete 
     */
    setScore(_score, success, fail, complete) {
        if (typeof (wx) != "undefined") {
            // console.log("_score=",_score);
            wx.setUserCloudStorage({
                KVDataList: [{ key: "score", value: _score + "" }],
                success: success || null,
                fail: fail || null,
                complete: complete || null,
            });
            // console.log("set score== end");
        }
    },

    /**
     * 
     */
    showFriendList() {
        if (typeof (wx) != "undefined") {
            wx.postMessage({
                rankType: 1,
            })
        }
    },

    /**
     * 
     * @param {string} shareTicket 
     */
    showGroupList(shareTicket) {
        if (typeof (wx) != "undefined") {
            wx.postMessage({
                shareTicket: shareTicket,
                rankType: 0,
            })
        }
    },

    /**
     * 
     */
    showGameResultList() {
        if (typeof (wx) != "undefined") {
            wx.postMessage({
                rankType: 2,
            })
        }
    },

    /**
     *  
     * @param {int} score 
     * @param {Number} x  pos.x
     * @param {Number} y  pos.y
     */
    checkSurpassFriend(score, x, y) {
        if (typeof (wx) != "undefined") {
            wx.postMessage({
                rankType: 3,
                score: score,
                x: x || 0,
                y: y || 0
            });
        }
    },


    checkWillSurpass(score, y) {
        if (typeof (wx) != "undefined") {
            wx.postMessage({
                rankType: 4,
                score: score,
                y: y || 500
            });
        }
    }
}